  &.fs-search-v2 {
    //display: flex;
    //flex-wrap: nowrap;
    .fs-search-buttons {
      display: flex;
    }
    .search-left {
    }

    .search-right {
      flex: 1;
    }

    .ant-form-inline {
      flex-wrap: wrap;
    }

    .fs-search-form {
      display: flex;
      align-items: center;
      flex-wrap: wrap;

      .ant-picker,
      .ant-select,
      .n-date-picker,
      .el-select,
      .el-date-editor {
        width: 100%;
      }

      .el-range-editor.el-input__wrapper {
        padding: 0;
      }

      .ant-btn-loading-icon {
        display: flex;
        margin-left: 2px;
        margin-right: 2px;
      }

      &.el-form--label-top {
        .el-form-item {
          display: block;
        }
      }

      .el-form-item {
        margin-bottom: 4px;
        margin-top: 4px;
        display: flex;
        align-items: center;
      }

      &.el-form--inline {
        .el-input {
          width: 100%;
        }
      }

      .el-form-item__content {
        display: flex;
        align-items: center;
      }
    }

    .fs-search-col {
      position: relative;
      // antdv
      .ant-form-item-explain,
      .el-form-item__error {
        float: left;
        position: absolute;
        bottom: 3px;
        right: 8px;
        pointer-events: none;
        top: auto;
        left: auto;
        visibility: hidden;
      }

      .el-form-item__error {
        bottom: 10px;
      }

      &:hover {
        .ant-form-item-explain,
        .el-form-item__error {
          visibility: visible;
        }
      }
    }
  }

  .n-form-item-blank {
  }
}
</style>
