<template>
  <component :is="ui.radioGroup.name" @change="onSelectedChange">
    <component
      :is="computedRadioName"
      v-for="item of computedOptions"
      ref="radioRef"
      :key="getValue(item)"
      :[ui.radio.value]="getValue(item)"
      v-bind="optionProps"
    >
      {{ getLabel(item) }}
    </component>
  </component>
</template>
<script lang="ts">
import { useDict } from "../../use/use-dict";
import { uiContext } from "../../ui";
import { ref, computed, defineComponent } from "vue";
import { useUi } from "../../use";
/**
 * 
 * el-radio-group|a-radio-group
 *
 */
export default defineComponent({
  name: "FsDictRadio",
  props: {
    /**
     * 
     */
    dict: {},
    /**
     * dict.data
     */
    options: { type: Array },

    /**
     * radio
     * antdvbutton
     * ,optionName
     */
    radioName: {},

    /**
     * 
     */
    optionName: {
      type: String
    },

    /**
     * 
     */
    optionProps: {
      type: Object,
      default() {
        return {};
      }
    },
    /**
     * DictData
     */
    transformDictData: {
      type: Function,
      default: undefined
    }
  },
  emits: [
    /**
     * 
     */
    "dict-change",

    /**
     * option
     */
    "selected-change",
    /**
     * 
     */
    "change"
  ],
  setup(props: any, ctx: any) {
    const { ui } = useUi();

    if (props.radioName) {
      console.warn("radioNameoptionName");
    }
    const computedRadioName = computed(() => {
      return props.optionName ?? props.radioName ?? ui.radio.name;
    });

    let usedDict = useDict(props, ctx, ui.radioGroup.modelValue);
    const computedOptions = usedDict.createComputedOptions();

    const onSelectedChange = (value: any) => {
      ctx.emit("change", value);
      if (value && value.target) {
        value = value.target.value;
      }
      const dict = usedDict.getDict();
      if (dict && dict.dataMap && dict.dataMap[value]) {
        const opt = dict.dataMap[value];
        ctx.emit("selected-change", opt);
      } else {
        ctx.emit("selected-change", null);
      }
    };

    return {
      ui,
      computedRadioName,
      ...usedDict,
      computedOptions,
      onSelectedChange
    };
  }
});
</script>
